<template>
  <div class="install-body">
    <h2 id="toc_0">一、说明</h2>

    <p>由于<code>zanProxy</code>默认不修改系统代理设置，所以在chrome上使用代理功能时需要依赖第三方chrome插件。</p>
    <p><code>zanProxy</code>依赖openssl生成证书，使用proxy前请先安装openssl(版本建议在0.9.8以上)。</p>

    <h2 id="toc_1">二、chrome插件安装</h2>

    <p>推荐安装SwitchyOmega<a
      href="https://chrome.google.com/webstore/detail/proxy-switchyomega/padekgcemlokbadohgkifijomclgjgif?hl=en-US"
      target="_blank">点击安装代理插件</a></p>

    <h4 id="toc_2">插件使用说明</h4>

    <ol>
      <li>安装完插件后请设置插件代理地址为<code>127.0.0.1</code>，代理协议: http，端口为<code>zanProxy</code>代理端口(默认8001)。</li>
      <li>如不清楚如何配置SwitchyOmega，请参考<a href="/help/chrome/"
                                     target="_blank">chrome代理设置指南</a></li>
    </ol>

    <h2 id="toc_3">三、证书安装</h2>

    <h4 id="toc_4">1. 为什么需要安装证书</h4>

    <p>由于<code>zanProxy</code>需要转发/mock https的请求，所以需要本地安装<code>ZanMock-proxy</code>https证书。</p>

    <h4 id="toc_5">2. 证书下载</h4>

    <ol>
      <li>mac系统请<a :href=url>点击下载到本地安装</a></li>
      <li>手机请扫码安装证书<img :src=imgUrl></li>
      <li>证书信任请参考<a href="/help/cert/" target="_blank">如何信任证书</a></li>
    </ol>
  </div>

</template>

<script>
  import qrcode from 'qrcode-js';
  export default{
    name: 'app',
    data(){
      var certUrl = 'http://' + location.hostname
        + ':' + (location.port || 80)
        + '/utils/rootCA.crt';
      return {
        url: certUrl,
        imgUrl: qrcode.toDataURL(certUrl, 4)
      }
    }
  }
</script>

