<template>
  <div>
    <div class='bread'>
      <strong>规则集列表</strong>
      <span style="margin-left: 50px">
           开启规则转发: <el-switch
        :value="$dc.ruleState"
        @input="$dc.setRuleState"
        on-color="#13ce66"
        off-color="#ff4949">
              </el-switch>
      </span>
    </div>
    <el-row :gutter="20" style="margin-bottom: 10px">
      <el-col :span="10" :offset="14" class="addrule-btn-wrap">
        <input type="file" @change="fileUpload" class="importfile"/>
        <el-button size="small">导入规则集</el-button>
        <el-button size="small" type="primary" @click='importRemoteRule'>导入远程规则</el-button>
        <el-button size="small" type="primary" @click='addRuleCollection'>新增规则集</el-button>
      </el-col>
    </el-row>
    <el-table border align='center' :data="$dc.ruleFileList" :row-class-name="rowClassName">
      <el-table-column prop="checked" label="启用" align="center" width="70">
        <template scope='scope'>
          <el-checkbox v-model="scope.row.checked" :disabled="!$dc.ruleState"
                       @change="onSelectionChange(scope.row.name,scope.row.checked)"></el-checkbox>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="名字" align="center" width="150" >
        <template scope='scope'>
          {{scope.row.name}}
        </template>
      </el-table-column>
      <el-table-column label="远程规则	" align="center" width="120" >
        <template scope='scope'>
          {{scope.row.meta.remote ? '是' : '-' }}
        </template>
      </el-table-column>
      <el-table-column prop="description" label="描述" align="center">
      </el-table-column>

      <el-table-column label="操作" :width="200" align="center" :context="_self">
        <template scope='scope'>
          <a :href="'#/editrule?name='+scope.row.name">
            <el-tooltip class="item" effect="dark" content="编辑规则" placement="top-start">
              <el-button type="info" icon='edit' size="mini">
              </el-button>
            </el-tooltip>
          </a>
          <el-tooltip class="item" effect="dark" content="导出规则" placement="top-start">
            <el-button type="info" icon='share' size="mini"
                       @click='onShareFile(scope.row,scope.$index)'>
            </el-button>
          </el-tooltip>
          <span>
            <el-button type="danger" icon='delete' size="mini" @click='onDeleteFile(scope.row,scope.$index)'>
            </el-button>
          </span>
          <span>
            <el-tooltip class="item" effect="dark" content="同步远程文件" placement="top-start">
              <el-button type="success" icon='information' size="mini"
                         v-if="scope.row.meta.remote && scope.row.meta.remoteETag && (scope.row.meta.ETag !== scope.row.meta.remoteETag)"
                         @click='onUpdateFile(scope.row,scope.$index)'>
              </el-button>
            </el-tooltip>
          </span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  import ruleApi from '../../../api/rule'
  import _ from 'lodash'
  export default {
    name: 'rulefilelist',
    methods: {
      rowClassName(row) {
        if (row.meta.remote) {
          return 'remote-rule';
        }
        return '';
      },
      onDeleteFile(row, index) {
        this.$confirm(`此操作将永久删除该文件: ${row.name}, 是否继续?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          ruleApi.deleteFile(row.name).then((response) => {
            var serverData = response.data;
            if (serverData.code == 0) {
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            } else {
              this.$message.error(`出错了，${serverData.msg}`);
            }
          });
        })
      },
      onShareFile(row, index) {
        if (!row.meta.remote) {
          window.location = '/rule/download?name=' + row.name;
        } else {
          this.$alert(row.meta.url, '规则URL', {
            confirmButtonText: 'OK',
            callback: action => {
            }
          });
        }
      },
      onUpdateFile(row, index) {
        this.$confirm(`确定更新: ${row.name} 么?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          ruleApi.getRemoteRuleFile(row.meta.url).then((response) => {
            var remoteFileResponse = response.data;
            var content = remoteFileResponse.data;
            content.meta = {
              "remote": true,
              "url": row.meta.url,
              "ETag": remoteFileResponse.headers['etag'],
              "remoteETag": remoteFileResponse.headers['etag']
            };
            content.name = content.name + '-remote';
            ruleApi.saveFile(content.name, content).then((response) => {
              var serverData = response.data;
              if (serverData.code == 0) {
                // 判断创建成功还是失败
                this.$message({
                  message: '恭喜你，更新成功',
                  type: 'success'
                });
              } else {
                _this.$message.error(`出错了，${serverData.msg}`);
              }
            });
          })
        })
      },
      importRemoteRule(){
        this.$prompt('请输入远程规则文件的url', '导入远程规则', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then((result) => {
          var url = result.value;
          console.log(url)
          ruleApi.getRemoteRuleFile(url).then((response) => {
            var remoteFileResponse = response.data;
            var content = remoteFileResponse.data;
            content.meta = {
              "remote": true,
              "url": url,
              "ETag": remoteFileResponse.headers['etag'],
              "remoteETag": remoteFileResponse.headers['etag']
            };
            content.name = content.name + '-remote';
            // 导入
            // 查找引用的变量
            var varNameList = ruleApi.getReferenceVar(content);
            var infoStr;
            if (varNameList.length > 0) {
              infoStr = `导入规则文件名为${content.name},引用变量【${varNameList.join('; ') }】请确保变量已经在工程路径配种中设置过`;
            } else {
              infoStr = `导入规则文件名为${content.name}`;
            }
            this.$alert(infoStr, '导入远程规则', {
              confirmButtonText: '确定',
              callback: action => {
                if (action == 'confirm') {
                  // 创建文件
                  ruleApi.saveFile(content.name , content).then((response) => {
                    var serverData = response.data;
                    if (serverData.code == 0) {
                      // 判断创建成功还是失败
                      this.$message({
                        message: '恭喜你，导入成功',
                        type: 'success'
                      });
                    } else {
                      this.$message.error(`出错了，${serverData.msg}`);
                    }
                  });
                }
              }
            });
          })
        })
      },
      onSelectionChange(name, checked) {
        ruleApi.setFileCheckStatus(name, checked).then((response) => {
          var serverData = response.data;
          if (serverData.code == 0) {
            this.$message({
              type: 'success',
              message: '设置成功!'
            });
          } else {
            this.$message.error(`出错了，${serverData.msg}`);
          }
        });
      },
      addRuleCollection(){
        this.$router.push('createrulefile');
      },
      fileUpload(evt){
        var _this = this;
        var files = evt.target.files;
        var file = files[0];
        if (!file || file.type.indexOf('json') < 0)return;


        var reader = new FileReader();
        reader.onload = function (e) {
          var contentStr = e.target.result;
          var content = JSON.parse(contentStr);

          // 判断是否存在同名规则，如果存在不允许导入
          var finded = _.find(_this.fileList, (file) => {
            return file.name == content.name;
          });
          if (finded) {
            _this.$message.error(`已经存在名为${content.name}的规则，请修改规则文件里的name字段,以及文件名`);
            return;
          }
          // 查找引用的变量
          var varNameList = ruleApi.getReferenceVar(content);
          var infoStr;
          if (varNameList.length > 0) {
            infoStr = `导入规则文件名为${content.name},引用变量【${varNameList.join('; ') }】请确保变量已经在工程路径配置中设置过值`;
          } else {
            infoStr = `导入规则文件名为${content.name}`;
          }
          _this.$alert(infoStr, '规则文件导入', {
            confirmButtonText: '确定',
            callback: action => {
              if (action == 'confirm') {
                // 创建文件
                ruleApi.saveFile(content.name, content).then((response) => {
                  var serverData = response.data;
                  if (serverData.code == 0) {
                    // 判断创建成功还是失败
                    _this.$message({
                      message: '恭喜你，导入成功',
                      type: 'success'
                    });
                  } else {
                    _this.$message.error(`出错了，${serverData.msg}`);
                  }
                });
              }
            }
          });
        };
        reader.readAsText(file);
      }
    }
  }

</script>
<style>
  .addrule-btn-wrap {
    text-align: right;
  }

  .importfile {
    position: absolute;
    width: 80px;
    height: 28px;
    opacity: 0;
    cursor: pointer;
  }
  .el-table .remote-rule {
    background-color: #eee;
  }

  .importfile::-webkit-file-upload-button {
    cursor: pointer;
  }
</style>



